.signin-container{
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: center;
          justify-content: center;
      margin-top:130px;
}
form{
      border: 1px solid #eee;
      padding: 12px;
      -webkit-box-shadow: 0px 0px 20px 5px #ddd;
              box-shadow: 0px 0px 20px 5px #ddd;
      background: white;
      border-radius: 5px;

}
.signin-container input{
      width:90vw;
      max-width: 350px;
}
.signin-container input[type=email] {
      margin-top: 25px;
      margin-bottom: 20px;
}
.signin-container input[type=password] {
      margin-top: 20px;
      margin-bottom: 20px;
}
.signin-container h1{
      text-align: center;
}

.signin-container input[type=submit] {
      background: rgba(255, 165, 48, 0.932);
      border-radius: 3px;
      border: none;
}

.signin-container input[type=submit]:hover {
      background: rgba(248, 147, 15, 0.932);
      -webkit-box-shadow: 0 0 8px rgba(253, 167, 55, 0.932);
              box-shadow: 0 0 8px rgba(253, 167, 55, 0.932);
}

.signin-container input[type=submit]:focus {
      background: rgba(245, 168, 69, 0.932);
      outline: rgba(248, 156, 36, 0.932);
      border: none;
      -webkit-box-shadow: 0 0 10px rgba(253, 167, 55, 0.932);
              box-shadow: 0 0 10px rgba(253, 167, 55, 0.932);
}


.signin-container input:focus {
      border: 1px solid rgba(255, 165, 48, 0.932);
      -webkit-box-shadow: 0 0 8px rgba(255, 165, 48, 0.932);
              box-shadow: 0 0 8px rgba(255, 165, 48, 0.932);
}



